<template><section>

<h2>notification 通知</h2>
<h3>基本用法</h3>
<demo-block>
                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="notify1()">默认</bee-button>
    <bee-button theme="primary" @click="notify1('warn')">warn</bee-button>
    <bee-button theme="success" @click="notify1('success')">success</bee-button>
    <bee-button theme="error" @click="notify1('error')">error</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
