    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify1()"</span>&gt;</span>默认<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'primary'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify1('warn')"</span>&gt;</span>warn<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'success'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify1('success')"</span>&gt;</span>success<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'error'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify1('error')"</span>&gt;</span>error<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
