      <span class="hljs-keyword">let</span> options = {
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一段提示文字。'</span>,
        <span class="hljs-attr">duration</span>: <span class="hljs-number">3</span>
      }

      type ? options.type = type : <span class="hljs-literal">null</span>

      <span class="hljs-keyword">this</span>.$_createNotify(options).show()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
