</div></demo-block>
<h3>非自动关闭</h3>
<demo-block>
                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="notify2()">默认</bee-button>
    <bee-button theme="primary" @click="notify2('warn')">warn</bee-button>
    <bee-button theme="success" @click="notify2('success')">success</bee-button>
    <bee-button theme="error" @click="notify2('error')">error</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify2()"</span>&gt;</span>默认<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'primary'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify2('warn')"</span>&gt;</span>warn<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'success'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify2('success')"</span>&gt;</span>success<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'error'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"notify2('error')"</span>&gt;</span>error<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
