</div></demo-block>
<h3>options</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>info,warn,success,error</td>
<td>info</td>
</tr>
<tr>
<td>duration</td>
<td>显示的时长</td>
<td>number</td>
<td>—</td>
<td>3</td>
</tr>
<tr>
<td>title</td>
<td>通知的标题</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>message</td>
<td>通知的信息</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.inline .btn--wp {
  margin: 0 10px 10px 0;
}
</style>
<script>
export default {
  data () {
    return {
      messages: []
    }
  },
  methods: {
    notify1 (type) {
      let options = {
        message: '这是一段提示文字。',
        duration: 3
      }

      type ? options.type = type : null

      this.$_createNotify(options).show()
    },

    notify2 (type) {
      let options = {
        title: '非自动',
        message: '非自动关闭',
        duration: 0
      }

      type ? options.type = type : null

      this.$_createNotify(options).show()
    }
  }
}
</script>